.employee {
    margin: 5px 0;
    display: flex;
    width: 100%;
    align-items: center;
    font-size: 14px;
}

.primary {
    flex-direction: row-reverse;
    justify-content: space-between;
}

.checkbox {
    cursor: pointer;
}

.primary .checkbox:checked + label {
    font-weight: bold;
}

.date label {
    padding-left: 15px;
}